.features {
    background-color: #111111;
    width: 100%;
    height: 280px;
    padding: 24px;
    border-radius: 8px;
    color: white;
    margin-left: 12px;
}
.features .container{
    display: flex;
    flex-direction: column;
}
.features .container .title {
    font-size: 24px;
    font-weight: 700;
}
.features .container .feature {
    display: flex;
    align-items: center;
    margin-top: 24px;
}
.features .container .featureValue {
    display: flex;
    flex-direction: column;
    margin-left: 16px;
}
.features .container .featureValue .value {
    font-size: 16px;
}
.features .container .featureValue .label {
    font-size: 18px;
    font-weight: 700;
}
